import { IActionModel, ActionType } from '../../../directives/action/models/action.model';
import { MethodType } from '../../../directives/action/models/interface.model';

export class ExecuteCmdForm {
    static key: string = "execute.cmd";

    actionModel: IActionModel;

    constructor() {
        let actionModel: IActionModel = {
            key: ExecuteCmdForm.key,
            type: ActionType.form,
            title: "",
            icon: "",
            form: {
                dataSchema: {
                    type: "object",
                    required: ["username", "password"],
                    properties: {
                        username: {
                            type: "string",
                            minLength: 4,
                            maxLength: 20,
                            title: "用户名"
                        },
                        eauth: {
                            type: "string",
                            default: "pam"
                        },
                        password: {
                            type: "string",
                            title: "密码",
                            minLength: 4,
                            maxLength: 20
                        }
                    }
                },
                formSchema: [{
                    key: "username",
                    type: "string",
                    placeHolder: "用户名",
                    icon: {
                        leftIcon: "account"
                    },
                    htmlClass: "md-icon-left md-block"
                }, {
                    key: "password",
                    type: "password",
                    icon: {
                        leftIcon: "lock"
                    },
                    htmlClass: "md-icon-left md-block"
                }]
            },
            refreshList: true,
            closeDialog: true,
            interfaces: [{
                key: "saltApiLogin",
                method: MethodType.POST,
                address: "",
                // port: "",
                path: "login",
                jpp: {
                    set: [{ "from": "/return", "to": "/data" }]
                },
                config: {
                    salt: true
                },
                isRestful: false
            }]
        };

        this.actionModel = actionModel;
        // return this.actionModel;
    }
}

export default (module: ng.IModule) => {
    const services = [ExecuteCmdForm];

    _.each(services, (model) => {
        module.service(model.key, model);
    });
}



